@import "valeurs-globales";
@import "mixin";
#checkIn{
  @include form(1rem 2rem);
  width: 65%;
  margin: 0 auto;
  h2{
    @include h2;
    text-align: center;
    margin-bottom: 2rem;
    span{
      color: #BCA474;
    }
  }
  form{

    input{
      @include inputForm(calc(90%/2));
    }
    select{
        @include inputSelectForm(calc((100% - 1.3rem)/3));
        text-align: center;
        option:hover{
          background-color: #BCA474;
        }
    }
    input[type="submit"]{
      @include button(calc((100% - 1rem)/3), .63rem 0);
      @include animButton(rgba(191, 10, 53, .9), #bf0a35, #e31b4b, #f03763);
      margin-top: 1rem;
      }
    // input+span{
    //   color: red;
    //   position: absolute;
    //   top: 23rem;
    //   left:50%;
    // }
  }
  p{
    color: red;
    text-align: center;
    margin-bottom: 1rem;
  }
}
